.GitRepoOverview {
    display: grid;
    
    grid-template-columns: 1fr 3fr 3fr 1fr;
    grid-template-rows: 1fr 2fr 4fr 2fr 1fr;

    text-align: left;

    margin-top: 20px;
    margin-bottom: 20px;

    border-width: 1px;
    border-style: solid;
    border-radius: 16px;
    border-color: lightgray;

    box-shadow: -1px 10px 19px -10px rgba(0,0,0,0.67);

    h3 {
        margin-top: 5%;
        margin-left: 5%;

        grid-column: 2 3;
        grid-row: 2;
    }
    
    p {
        margin-top: 5%;
        margin-left: 5%;
    }

    .BottomSection {
        float: bottom;
    }
}

