.HeaderBar {
    display: flex;
    width: 100vw;

    z-index: 100;

    transition: ease;
    transition-duration: 0.25s;
}

a:link {
    color: black;
    text-decoration: none;
}

a:visited {
    color: black;
    text-decoration: none;
}

@keyframes slideInFromLeft {
    0% {
        opacity: 0%;
        transform: translateX(-100%);
    }
    100% {
        opacity: 100%;
        transform: translateX(0);
    }
}

@keyframes slideInFromRight {
    0% {
        opacity: 0%;
        transform: translateX(100%);
    }
    100% {
        opacity: 100%;
        transform: translateX(0);
    }
}

@keyframes slideInFromTop {
    0% {
        opacity: 0%;
        transform: translateY(-100%);
    }
    100% {
        opacity: 100%;
        transform: translateY(0);
    }
}

// lowest width i am willing to support
$height-360: 20vh;
$min-height-360: 128px;
@media screen and (max-width: 450px) {
    .HeaderBar {
        position: sticky;

        top: -($height-360/2);

        height: $height-360;
        min-height: $min-height-360;

        flex-direction: column;

        align-items: center;
        vertical-align: middle;

        .dumb-div {
            position: fixed;
            top: 0;

            width: 100vw;
            height: ($height-360/2);

            min-height: ($min-height-360/2);

            background-color: white;

            h1 {

                margin-top: ($height-360/8);
                text-align: center;

                animation-duration: 1s;
                animation-timing-function: ease-in-out;
                animation-delay: 0s;
                animation-iteration-count: 1;
                animation-name: slideInFromTop;
            }
        }

        .MenuBar {
            display: flex;

            width: 100vw;
            height: ($height-360/2);
            min-height: ($min-height-360/2);

            padding: 0;
            margin-top: ($height-360/2);

            margin-bottom: 0;

            justify-content: space-evenly;
            flex-direction: row;
            align-items: center;

            background-color: white;
            box-shadow: 0px 0px 15px 8px rgba(84,84,84,0.34);

            list-style-type: none;

            animation-duration: 1s;
            animation-timing-function: ease-in-out;
            animation-delay: 0s;
            animation-iteration-count: 1;
            animation-name: slideInFromTop;
        }
    }
}

// anything else
$height-450: 10vh;
$min-height-450: 75px;
@media screen and (min-width: 451px) {
    .HeaderBar {
        position: fixed;

        top: 0;

        height: $height-450;
        min-height: $min-height-450;

        flex-direction: row;

        justify-content: space-between;
        align-items: center;
        vertical-align: middle;

        background-color: rgba(255, 255, 255, 0);
        backdrop-filter: saturate(180%) blur(20px);

        border-bottom: 10px;
        border-bottom-color: gray;

        box-shadow: 0px 0px 15px 8px rgba(84,84,84,0.34);

        .dumb-div {
            position: relative;
            display: flex;

            width: 50%;
            height: (100%);

            margin-left: 24px;

            background-color: rgba(255, 255, 255, 0);

            flex-direction: row;

            justify-content: start;
            flex-direction: row;
            align-items: center;

            animation-duration: 1s;
            animation-timing-function: ease-out;
            animation-delay: 0s;
            animation-iteration-count: 1;
            animation-name: slideInFromLeft;

            h1 {
                height: fit-content;
                min-font-size: 50px;
                margin: 8px
            }

            img {
                margin: 8px
            }
        }

        .MenuBar {
            display: flex;

            width: fit-content;
            height: ($height-360/2);
            min-height: ($min-height-360/2);

            padding: 0;
            margin-top: 0;
            margin-right: 48px;

            margin-bottom: 0;

            justify-content: right;
            flex-direction: row;
            align-items: center;

            background-color: rgba(255,255,255,0);

            list-style-type: none;

            animation-duration: 1s;
            animation-timing-function: ease-in-out;
            animation-delay: 0s;
            animation-iteration-count: 1;
            animation-name: slideInFromRight;

            li {
                margin: 16px;
            }
        }
    }
}