.sect {
    display: block;
        
    padding: 10px;

    margin-top: 50px;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;

    border-style: solid;
    border-width: 1px;
    border-radius: 16px;
    border-color: lightgray;

    transition: ease-in-out;
    transition-duration: 300ms;
}

.sect:hover {
    transform: scale(1.02);
    transition: ease-in-out;
    transition-duration: 300ms;
    box-shadow: 0px 5px 14px -1px rgba(0,0,0,0.3);
}
