/* this is to be put in the new image loader*/


@media screen and (min-width: 1200px) {

    .HomePage {

        
        .ProfileImage {
            width: 200px;
            height: 200px;
            border-radius: 100px;
            box-shadow: 0px 5px 14px -1px rgba(0,0,0,0.3);
        }
        
        .RepoSect {
            display: grid;
        
            grid-template-columns: 46% 46%;
            gap: 8%;
        
            width: 75%;
        
            padding: 0;
            margin: auto;
        }
    }
}

@media screen and (max-width: 1199px) and (min-width: 700px) {
    .ProfileImage {
        width: 200px;
        height: 200px;
        border-radius: 100px;
        box-shadow: 0px 5px 14px -1px rgba(0,0,0,0.3);
    }
        
    .RepoSect {
        display: block;
    
        width: 80%;
    
        padding: 0;
        margin: auto;
    }
}

@media screen and (max-width: 699px) {
    .ProfileImage {
        width: 200px;
        height: 200px;
        border-radius: 100px;
        box-shadow: 0px 5px 14px -1px rgba(0,0,0,0.3);
    }
    
    .RepoSect {
        display: block;
    
        width: 98%;
    
        padding: 0;
        margin: auto;
    }
}

