.Gallery {
    display: grid;

    width: 96%;

    grid-template-columns: repeat(3, 1fr);

    gap: 20px;

    margin: auto;

    justify-items: center;
}


/* this is to be put in the new image loader*/
.Gallery .images {
    width: 100%;
    height: 100%;

    border-radius: 16px;

    object-fit: cover;

    box-shadow: -1px 10px 19px -10px rgba(0,0,0,0.67);

    transition: ease-in-out;
    transition-duration: 300ms;
}

.Gallery .images:hover {
    transform: scale(1.1);
    transition: ease-in-out;
    transition-duration: 300ms;
}