.images {
    width: 100%;
    height: 100%;

    border-radius: 16px;

    object-fit: cover;

    box-shadow: -1px 10px 19px -10px rgba(0,0,0,0.67);

    transition: ease-in-out;
    transition-duration: 300ms;
    
    :hover {
        transform: scale(1.1);
        transition: ease-in-out;
        transition-duration: 300ms;
    }
}